

.FormBackground {
   
    overflow: hidden;
    position:absolute;
    
    width:75%;
    margin:3px;
    padding:15px;
   
}
.FormElement {
   
    margin-top: 17px;
    
   max-width: 70%;
    margin-left: 10px;
    padding:7px;
   
    
}

label {
  font-size:30px;
  margin-right:7px;
}

input, select{
    margin:5px;
    background-color:#005B9A;
    color:white;
    font-size:25px
}
::placeholder{
    color:white;
    opacity: 1;
}

::-ms-input-placeholder { /* Edge 12 -18 */
    color:white;
    opacity: 1;
  }

.submitBtn{
    background-color:  #005B9A;;
    color:white;
    border:none;
    transition: 0.3s;
    font-size: 25px;
    border-radius:10px;
    padding: 12px;
}

    .submitBtn:hover {
        cursor: pointer;
        
       background-color:grey;
    }

.btnHolder {
    position: absolute;
    right: 5px;
    bottom: 5px;
}